import React from 'react'
import styled from 'styled-components'

import Modal from 'react-modal';
import useSound from 'use-sound';
import { Box, Typography } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';

import meta from '../../../assets/wallet-icon/metamask.svg'
import connect from '../../../assets/wallet-icon/wallet-connect.svg'

import sounds from '../../../assets/t-rex-roar.mp3'


const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        background: '#181d22',
        borderRadius: '1px',
        width: '100%',
        maxWidth: '500px',
        border: 'none',
    },
};


const Connectbtn = styled.div` 
    cursor: pointer; 
    background: ${({ theme }) => theme.bgBtns};
    padding: 10px 20px;
    border-radius: 5px;
    width:100%;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    transition: 0.5s; 
    &:hover { 
        background: ${({ theme }) => theme.bgLighter};
        color: ${({ theme }) => theme.text};
    }
`
const Metabtn = styled.div` 
    cursor: pointer; 
    background: ${({ theme }) => theme.bgLighter};
    padding: 24px 4rem;
    border-radius: 5px;
    width:100%;
    font-size: 16px;
    font-weight: 600; 
    text-align:center;
    transition: 0.5s; 
    &:hover { 
        background: ${({ theme }) => theme.bgBtns};
        color: ${({ theme }) => theme.text};
    }
        
`


const ConnectButton = () => {

    const [play] = useSound(sounds);
    let subtitle;
    const [modalIsOpen, setIsOpen] = React.useState(false);

    function openModal() {
        play();
        setIsOpen(true);
    }

    function afterOpenModal() {
        // references are now sync'd and can be accessed.
        subtitle.style.color = '${({ theme }) => theme.bgBtns}';
    }

    function closeModal() {
        setIsOpen(false);
    }
    return (
        <div>
            <Connectbtn variant='contained' className='explorebtns' onClick={openModal}>
                Connect Wallet
            </Connectbtn>
            <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <Box display='flex' alignItems='center' justifyContent='space-between'>
                    <Typography  variant='h4' color='#fff' ref={(_subtitle) => (subtitle = _subtitle)}>Wallet Connect</Typography>
                    <button onClick={closeModal} style={{ background: 'transparent', border: 'none', cursor: 'pointer', }}>
                        <CloseIcon onClick={play} sx={{color:'#fff'}} />
                    </button>
                </Box>

                <Box paddingY='2rem'>
                    <Box paddingY='1rem'>
                        <Typography textAlign='center' color='#fff'>
                            Please select a wallet to connect for start Minting your NFTs
                        </Typography>
                    </Box>
                    <Box marginBottom='10px'>
                        <Metabtn onClick={play} variant='contained' className='explorebtns'>
                            <Box>
                                <img src={meta} width='40px' />
                            </Box>
                            <Typography variant='h5' gutterBottom>
                                MetaMask
                            </Typography>
                            <Typography>
                                Connect to your MetaMask Wallet
                            </Typography>
                        </Metabtn>
                    </Box>
                    <Box>
                        <Metabtn onClick={play} variant='contained' className='explorebtns'>
                            <Box>
                                <img src={connect} width='40px' />
                            </Box>
                            <Typography variant='h5' gutterBottom>
                                MetaMask
                            </Typography>
                            <Typography>
                                Connect to your MetaMask Wallet
                            </Typography>
                        </Metabtn>
                    </Box>
                </Box>
            </Modal>

        </div>
    )
}

export default ConnectButton;