import { Box, Container, Grid, Typography } from '@mui/material';
import React from 'react'
import styled from 'styled-components';
import EcosystemBalance from './EcosystemBalance';


const Ecosys = styled.div`
    background: ${({ theme }) => theme.boxbg};
    position: relative;
`
const Spans = styled.span`
    color: ${({ theme }) => theme.bgBtns};
    font-weight:600;
`
const Leverage = styled.div`
    color: ${({ theme }) => theme.textmulti};
    font-size:20px;
`

const EcoSystem = () => {
    return (
        <div>
            <Ecosys className='eccosarea'>
                <Box paddingY='4rem'>
                    <Container maxWidth='lg'>
                        <Grid container spacing={2}>
                            <Grid item lg={12} md={12} sm={12} xs={12}>
                                <Box textAlign='center' position='relative' >
                                    <Typography variant='h3' gutterBottom component='h3' fontWeight='600'>
                                        Mi<Spans>nt Ecosys</Spans>tem
                                    </Typography>
                                    <Leverage >
                                    Contrary to popular belief, Lorem Ipsum is not simply random text.
                                    </Leverage>
                                </Box>
                            </Grid>
                        </Grid>
                    </Container>
                    <Box paddingY='3rem'>
                        <EcosystemBalance />
                    </Box>
                </Box>
            </Ecosys>
        </div>
    )
}


export default EcoSystem;