import { Box, Typography } from '@mui/material';
import React from 'react'
import styled from 'styled-components';

import righticon from '../../../assets/mint-right-text-icon.svg'

const Mintnew = styled.button`
cursor: pointer; 
    background: ${({ theme }) => theme.bgBtns};
    padding: 10px 4rem;
    border-radius: 1px;
    width:100%;
    font-size: 20px; 
    text-align:center;
    transition: 0.5s; 
    &:hover { 
        background: ${({ theme }) => theme.bgLighter};
        color: ${({ theme }) => theme.text};
  `


const MintRight = () => {
    return (
        <div>
            <Box>
                <Box className="content_header">
                    <Box>
                        <Typography variant='h5' fontWeight='500' color='#fff'>
                            WHITELIST : SOLDOUT<span>
                            <img src={righticon} alt="icon" /></span>
                        </Typography>
                    </Box>
                    <Box paddingTop='10px'>
                        <Typography variant='h5' fontWeight='500' color='#fff'>
                            Presale : SOLDOUT<span>
                            <img src={righticon} alt="icon" /></span>
                        </Typography>
                    </Box>
                    <Box paddingY='2rem'>
                        <Typography variant='h2' fontWeight='bold' gutterBottom component='h2' color='#fff'>
                            PUBLIC MINT LIVE
                        </Typography>
                    </Box>
                    <Box>
                        <Typography variant='h5' fontWeight='600' gutterBottom component='h5' color='#fff'>
                            PUBLIC MINT END IN
                        </Typography>

                        <Typography variant='h4' fontWeight='600' gutterBottom component='h4' color='#fff'>
                             6D : 20H : 30M : 32s
                        </Typography>
                    </Box>
                    <Box paddingY='2rem'>
                        <Typography variant='h5' fontWeight='500' gutterBottom component='h5' color='#fff'>
                            Max 5 NFTs per wallet
                        </Typography> 
                        <Typography variant='h5' fontWeight='500' gutterBottom component='h5' color='#fff'>
                            PRICE 0.09 ETH + GAS
                        </Typography> 
                        <Typography variant='h5' fontWeight='500' gutterBottom component='h5' color='#fff'>
                            MINT IS LIVE UNTIL 25 Sept. 04:00H
                        </Typography> 
                    </Box>
                </Box>
            </Box>
        </div>
    )
}

export default MintRight;