import { Box, Typography } from '@mui/material';
import React from 'react'
import styled from 'styled-components';


const Assetsstaked = styled.p`
    color: ${({ theme }) => theme.textmulti};
`


const RewardsPaid = () => {
    return (
        <div>
            <Box>
                <Typography variant='h3' fontWeight='bold' gutterBottom component='h3'>
                    <span style={{fontWeight:'bold'}}>74</span> /
                    <span style={{fontWeight:'bold'}}>5555 MINTED</span>
                </Typography>
            </Box>
        </div>
    )
}

export default RewardsPaid;