import React, { useState } from 'react'
import {
  BrowserRouter,
  Routes,
  Route,
} from "react-router-dom";
import styled, { ThemeProvider } from 'styled-components';
import { darkTheme, lightTheme } from './Components/utils/Theme';
import Index from './Components/View/MainPage/Index';
import HeaderBar from './Components/Layouts/Headers/HeaderBar'
import Footer from './Components/Layouts/Footers/Footer'
import FAQ from './Components/View/FAQs/FAQ';
import MintNft from './Components/Layouts/MintPage/MintNft';


const Wrapper = styled.div`
  background: ${({ theme }) => theme.bg};  
  color: ${({ theme }) => theme.text}; 
`


const App = (props) => {
  const [darkMode, setDarkMode] = useState(true);


  // const isAdminPage = window.location.href.match(/admin/)
  //   ||
  //   window.location.href.match(/BNBAdmin/)
  //   ;

  return (
    <ThemeProvider theme={darkMode ? darkTheme : lightTheme}>
      <BrowserRouter>
        <HeaderBar darkMode={darkMode} setDarkMode={setDarkMode} />
        <Wrapper>
          <Routes>
            <Route path="/" element={<Index />}>
              <Route index element={<Index />} />
            </Route>
            <Route path='/mint-nft' element={<MintNft />} /> 
          </Routes>
        </Wrapper>
 
        <Footer darkMode={darkMode} setDarkMode={setDarkMode} /> 
      </BrowserRouter>
    </ThemeProvider>
  )
}

export default App;