import { Box, Container, Grid, Typography } from '@mui/material';
import React from 'react'
import styled from 'styled-components';
import useSound from 'use-sound';
import sounds from '../../../assets/t-rex-roar.mp3'

import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import { Navigation } from 'swiper';
import './Network.css';


import bea from '../../../assets/Hot-Collection/bear.png'
import bnb from '../../../assets/Hot-Collection/coll-3.jpg'

const Cards = styled.div`
    background: ${({ theme }) => theme.boxbg};
    border-radius: 12px;
    padding: 10px; 
    width: 100%; 
    height: 100%;
    position: relative;
    overflow: hidden;
`
const StartStaking = styled.button`
    padding: 10px 20px;
    border: 1px solid ${({ theme }) => theme.bgBtns};
    background: ${({ theme }) => theme.bgBtns};
    color:  ${({ theme }) => theme.bg};
    border-radius: 10px;
    font-size: 20px;
    cursor: pointer;
    transition: 0.5s;
    width: 100%;
    &:hover {
        background: ${({ theme }) => theme.bg};
        color: ${({ theme }) => theme.bgBtns};
        letter-spacing:2px;
        border: 1px solid ${({ theme }) => theme.bgBtns}
    }
`
const Img = styled.img`
    width: 100%;
    height: 230px;
    border-radius:12px;
    object-fit: cover;
`

const Inputbase = styled.input`
    border: 1px solid #ff9800; 
    background: transparent;
    padding:13px;
    border-radius:5px;
    color: #fff;
    width: 100%;
    font-size:20px
`

const Network = () => {

    
    const [play] = useSound(sounds);

    return (
        <div>
            <Box paddingY='3rem'>
                <Container maxWidth='xl'>
                    <Grid container spacing={2}>
                        {mintf.map((items) => (
                            <Grid key={items} item lg={3} md={3} sm={12} xs={12}>
                                <Box>
                                    <Cards className='boxfirst'>
                                        <Box className='secarea'>
                                            <Box textAlign='center' position='relative'>
                                                <Img src={items.tokenimgs} alt='' />
                                            </Box>
                                            <Box padding='15px' position='relative'>
                                                <Typography variant='h5' fontWeight='normal' gutterBottom component='h5'>
                                                    {items.tokenname}
                                                </Typography>
                                                <Box display='flex' alignItems='center' justifyContent='space-between'>
                                                    <Typography variant='h6' fontWeight='normal' component='h6'>
                                                        {items.tokenprice}
                                                    </Typography>
                                                    <Typography variant='h6' color='#8c8c8c' fontWeight='normal' component='h6'>
                                                        {items.stacks}
                                                    </Typography>
                                                </Box>
                                            </Box>
                                            <Box paddingX='15px' position='relative'>
                                                <Box display='flex' alignItems='center' justifyContent='space-between'>
                                                    <Typography variant='h6' fontWeight='normal' component='h6'>
                                                        {items.totals}
                                                    </Typography>
                                                    <Typography variant='h6' color='#8c8c8c' fontWeight='normal' component='h6'>
                                                        {items.priceto}
                                                    </Typography>
                                                </Box>
                                            </Box>
                                            <Box padding='15px'>
                                                <Inputbase type='text' placeholder='*******' />
                                            </Box>
                                            <Box paddingX='15px'>
                                                <StartStaking onClick={play}>
                                                    {items.btns}
                                                </StartStaking>
                                            </Box>
                                        </Box>
                                    </Cards>
                                </Box>
                            </Grid>
                        ))}
                    </Grid>
                </Container>
            </Box>
        </div>
    )
}

export default Network;


