import { Box, Drawer, IconButton, List, ListItemButton, ListItemIcon, ListItemText, Typography } from '@mui/material';
import React, { useState } from 'react'
import MenuRoundedIcon from '@mui/icons-material/MenuRounded';
import { Link } from 'react-router-dom';
// import styled from 'styled-components';
import ConnectButton from './ConnectButton';


const Pages = [
  {
    titles: 'Home',
    route: './'
  },
  {
    titles: 'Mint NFT',
    route: './mint-nft'
  },
 
]

// const Connectbtn = styled.div` 
//     cursor: pointer; 
//     background: ${({ theme }) => theme.bgBtns};
//     padding: 10px 20px;
//     border-radius: 5px;
//     font-size: 16px;
//     font-weight: 700;
//     color: #fff;
// `

const DrawerComponent = () => {

  const [openDrawer, setOpenDrawer] = useState(false);


  return (
    <React.Fragment>
      <Drawer open={openDrawer} onClose={() => setOpenDrawer(false)} >
        <List>
          <Box padding='1rem'>
            <Typography>
              Network
            </Typography>
          </Box>
          {
            Pages.map((page, index) => (

              <ListItemButton key={page} onClick={() => setOpenDrawer(false)}>
                <ListItemIcon>
                  <ListItemText>
                    <a href={page.route} target='_blank' style={{ textDecoration: 'none', color: '#000' }}>
                      {page.titles}
                    </a>
                  </ListItemText>
                </ListItemIcon>
              </ListItemButton>
            ))
          }

        </List>
        {/*<Box sx={{ margin: '10px' }} >
          <ConnectButton />
        </Box>*/}
      </Drawer>
      <Box marginLeft='auto'>
        <IconButton onClick={() => setOpenDrawer(!openDrawer)}>
          <MenuRoundedIcon sx={{ color: '#ff9800' }} />
        </IconButton>
      </Box>
    </React.Fragment>
  )
}

export default DrawerComponent;