import React, { useState , useEffect} from 'react'
import { Toolbar, Box, useMediaQuery, useTheme, Container } from '@mui/material' 
import DarkModeOutlinedIcon from '@mui/icons-material/DarkModeOutlined';
import WbSunnyRoundedIcon from '@mui/icons-material/WbSunnyRounded';

import logo from './mm3d.png'
import DrawerComponent from './DrawerComponent';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import NavBar from './NavBar';
import ConnectButton from './ConnectButton';

import withRouter from '../../../withRouter';

const Navbarmain = styled.div`
    background: ${({ theme }) => theme.bg}; 
    position:sticky;
    top:0;
    display:flex;
    align-items:center;
    z-index:10;
    // height:100px;
`

const Logo = styled.div`  
    gap:5px; 
`
const Img = styled.img` 
    width:80px
`
const DarkLight = styled.div`
    margin-left: 1rem;
    cursor: pointer;
`
const Connectbtn = styled.div` 
     
        cursor: pointer; 
        background: ${({ theme }) => theme.bgBtns};
        padding: 10px 20px;
        border-radius: 5px;
        font-size: 16px;
        font-weight: 700;
        color: #fff; 
    $:hover { 
        background: ${({ theme }) => theme.bg};
    }
`



const HeaderBar = ({ darkMode, setDarkMode, router }) => {

    const [value, setValue] = useState();
    const theme = useTheme();
    console.log(theme);

    const isMatch = useMediaQuery(theme.breakpoints.down('md'));

    useEffect(() => {
      console.log(router)
    
      
    }, [router])
    

    return ( router.location.pathname.match(/admin/) ||
     router.location.pathname.match(/BNBAdmin/) ? <></> : (
        <React.Fragment>
            <Navbarmain>
                <Container maxWidth='xl'>
                    <Toolbar>
                        <Box paddingY='8px' component={Link} to='/'>
                            <Logo>
                                <Img src={logo} alt='' />
                            </Logo>
                        </Box>
                        {
                            isMatch ? (
                                <>
                                    <DrawerComponent />
                                    <DarkLight onClick={() => setDarkMode(!darkMode)}>
                                        {darkMode ? <Box display='flex' alignItems='center'>
                                            <DarkModeOutlinedIcon sx={{ color: '#fff', gap: '10px', }} />
                                        </Box> :
                                            <Box display='flex' alignItems='center'>
                                                <WbSunnyRoundedIcon sx={{ color: '#151516', gap: '10px', }} />
                                            </Box>
                                        }
                                    </DarkLight>
                                </>
                            ) : (
                                <>
                                    <Box marginLeft='auto'>
                                        <NavBar />
                                    </Box>
                                    <Box sx={{ marginLeft: '3rem' }} >
                                        <ConnectButton />
                                        </Box> 
                                    <DarkLight onClick={() => setDarkMode(!darkMode)}>
                                        {darkMode ? <Box display='flex' alignItems='center'>
                                            <WbSunnyRoundedIcon sx={{ color: '#fff', gap: '10px', }} />
                                        </Box> :
                                        <Box display='flex' alignItems='center'>
                                        <DarkModeOutlinedIcon sx={{ color: '#ff9800', gap: '10px', }} />
                                        </Box>
                                        }
                                    </DarkLight>
                                </>
                            )}
                    </Toolbar>
                </Container>
            </Navbarmain>
        </React.Fragment>
        )
    )
}

export default withRouter (HeaderBar);