import React from 'react'
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import { Box } from '@mui/material';

const Baritems = styled.div`
    color: ${({ theme }) => theme.text};
    display: flex;
    gap:30px;
    padding: 13px;
`
const Menuitems = styled.div`
    display:flex;
`

const Itemsl = styled.div`
    color: ${({ theme }) => theme.text}; 
    font-size: 18px;
    font-weight: normal;
    transition: 0.5s;
    position: relative;
    &:hover {
        color: ${({ theme }) => theme.bgBtns};
    }
`



const NavBar = () => {
    return (
        <div>
            <Baritems>
                <Menuitems>
                    <Box className='topmenus'>
                        <Link to='/' style={{ textDecoration: 'none' }}>
                            <Itemsl className='dropdown'>
                                Home
                            </Itemsl>
                        </Link>
                    </Box>
                </Menuitems>
                <Menuitems>
                    <Box className='topmenus'>
                        <Link to='/mint-nft' style={{ textDecoration: 'none' }}>
                            <Itemsl className='dropdown'>
                                Mint NFT
                            </Itemsl>
                        </Link>
                    </Box>
                </Menuitems>
            </Baritems>
        </div>
    )
}

export default NavBar;