import { Box, Container, Grid } from '@mui/material';
import React from 'react'
import MintLeft from './MintLeft';
import MintRight from './MintRight';

const MintNft = () => {
    return (
        <div>
            <Box className='ming-bg'>
                <Box  sx={{ paddingY: { md: '3rem', xs: '2rem' } }}>
                    <Container maxWidth='lg'>
                        <Grid container spacing={2} alignItems='center'>
                            <Grid item lg='5' md='6' sm='12' xs='12'>
                                <Box>
                                    <MintLeft />
                                </Box>
                            </Grid>
                            <Grid item lg='6' md='6' sm='12' xs='12' sx={{marginLeft:{md:'2rem', xs:'0rem'}}}>
                                <Box>
                                    <MintRight />
                                </Box>
                            </Grid>
                        </Grid>
                    </Container>
                </Box>
            </Box>
        </div>
    )
}

export default MintNft;