import { Box, Container, Grid, Typography } from '@mui/material';
import React from 'react'
import styled from 'styled-components';


import balancer from '../../../assets/Token-Icon/balancer.svg'
import quickswap from '../../../assets/Token-Icon/quickswap.svg'
import beefy from '../../../assets/Token-Icon/beefy.svg'
import meshswap_2 from '../../../assets/Token-Icon/meshswap_2.svg'
import quickswaps from '../../../assets/Token-Icon/quickswaps.svg'
import beethoven from '../../../assets/Token-Icon/beethoven.svg'

const Cards = styled.div`
    background: ${({ theme }) => theme.bg};
    border-radius: 12px;
    padding: 2rem 20px;
    height: 170px;
` 


const EcosystemBalance = () => {
    return (
        <div>
            <Box paddingY='3rem'>
                <Container maxWidth='lg'>
                    <Grid container spacing={2}>
                        {tokens.map((items) => (
                            <Grid key={items} item lg={4} md={4} sm={6} xs={12} margin='auto'>
                                <Cards className='boxbalance'>
                                    <Box className='balancearea' display='flex' alignItems='center' sx={{gap:'10px'}}>
                                        <Box position='relative'>
                                            <img src={items.tokenimgs} width='50px' />
                                        </Box>
                                        <Box position='relative'>
                                            <Typography variant='h5' fontWeight='400' gutterBottom component='h5'>
                                                {items.headering}
                                            </Typography> 
                                            <Typography variant='body1' color='#8c8c8c' fontWeight='400' component='body1'>
                                                {items.stacks}
                                            </Typography>
                                        </Box>
                                    </Box>
                                </Cards>
                            </Grid>
                        ))}
                    </Grid>
                </Container>
            </Box>
        </div>
    )
}

export default EcosystemBalance;


const tokens = [
    {
        tokenimgs: balancer,
        headering: 'Balancer', 
        stacks: 'Automated portfolio manager and trading platform',
    },
    {
        tokenimgs: quickswap,
        headering: 'Quickswap', 
        stacks: 'Next-generation layer-2 decentralized exchange and automated market maker',
    },
    {
        tokenimgs: beefy,
        headering: 'Beefy Finance', 
        stacks: 'The multichain yield optimizer',
    },
    {
        tokenimgs: meshswap_2,
        headering: 'Meshswap', 
        stacks: 'Unique AMM DEX protocol (swap, lend, farm & leverage, and stake)',
    },
    {
        tokenimgs: quickswaps,
        headering: 'Liquid Driver', 
        stacks: 'High-Yield liquidity mining DAPP',
    },
    {
        tokenimgs: beethoven,
        headering: 'Beethoven X', 
        stacks: 'First next-generation AMM protocol on Fantom Opera',
    },
]
