import { Box, Divider, Typography } from '@mui/material';
import React from 'react'
import styled from 'styled-components';
import Modal from 'react-modal';
import useSound from 'use-sound';
import sounds from '../../../assets/t-rex-roar.mp3'
import mints from '../../../assets/Hot-Collection/bear.png'
import CloseIcon from '@mui/icons-material/Close';

const Hr = styled.hr` 
    border: 1px solid ${({ theme }) => theme.textmulti}; 
`

const Inputbase = styled.input`
    border: 1px solid #ff9800; 
    background: transparent;
    padding:13px;
    border-radius:5px;
    color: #fff;
    width: 100%;
    font-size:20px
`
const Mintnew = styled.button`
cursor: pointer; 
    background: #ff9800;
    padding: 10px;
    display: flex;
    border-radius: 1px;
    font-size: 20px;
    text-align: center; 
    transition: 0.5s;
    width: 130px;
    text-align: center;
    align-items: center;
    border-radius: 5px;
    justify-content: center; 
`


const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
        background: '#181d22',
        borderRadius: '1px',
        width: '100%',
        maxWidth: '500px',
        border: 'none',
    },
};


const MintLeft = () => {

    const [play] = useSound(sounds);
    let subtitle;
    const [modalIsOpen, setIsOpen] = React.useState(false);

    function openModal() {
        play();
        setIsOpen(true);
    }

    function afterOpenModal() {
        // references are now sync'd and can be accessed.
        subtitle.style.color = '${({ theme }) => theme.bgBtns}';
    }

    function closeModal() {
        setIsOpen(false);
    }


    return (
        <div>
            <Box>
                <Box paddingX='1rem'>
                    <img src={mints} width='100%' alt='mint image' style={{ borderRadius: '12px' }} />
                </Box>
                <Box padding='1rem'>
                    <Box display='flex' alignItems='center' justifyContent='space-between'>
                        <Typography variant='h6' color='#fff'>
                            Remaining
                        </Typography>
                        <Typography variant='h6' color='#fff'>
                            <span>4583</span> /
                            <span>9999</span>
                        </Typography>
                    </Box>
                    <Box marginY='10px'>
                        <Hr />
                    </Box>
                    <Box display='flex' alignItems='center' justifyContent='space-between'>
                        <Typography variant='h6' color='#fff'>
                            Price
                        </Typography>
                        <Typography variant='h6' color='#fff'>
                            0.55 BNB
                        </Typography>
                    </Box>
                    <Box marginY='1rem' display='flex' alignItems='center'>
                        <Inputbase type='number' placeholder='0.30 BNB' required />
                        <Box>
                            <Mintnew onClick={openModal}>
                                Mint Now
                            </Mintnew>
                        </Box>
                    </Box>

                </Box>
            </Box>
            <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <Box display='flex' alignItems='center' justifyContent='space-between'>
                    <Typography variant='h4' color='#fff' ref={(_subtitle) => (subtitle = _subtitle)}></Typography>
                    <button onClick={closeModal} style={{ background: 'transparent', border: 'none', cursor: 'pointer', }}>
                        <CloseIcon onClick={play} sx={{ color: '#fff' }} />
                    </button>
                </Box>

                <Box padding='1rem'>
                    <Box paddingBottom='1rem'>
                        <Typography variant='h4' fontWeight='700' textAlign='center' color='#fff'>
                            COLLECT YOUR NFT BEFORE END
                        </Typography>
                    </Box>
                    <Box>
                        <Box textAlign='center'>
                            <img src={mints} width='230px' alt='mint image' style={{ borderRadius: '12px' }} />
                        </Box>
                        <Box>
                            <Box display='flex' alignItems='center' justifyContent='space-between'>
                                <Typography variant='h6' color='#fff'>
                                    Remaining
                                </Typography>
                                <Typography variant='h6' color='#fff'>
                                    <span>4583</span> /
                                    <span>9999</span>
                                </Typography>
                            </Box>
                            <Box marginY='10px'> 
                            </Box>
                            <Box display='flex' alignItems='center' justifyContent='space-between'>
                                <Typography variant='h6' color='#fff'>
                                    Price
                                </Typography>
                                <Typography variant='h6' color='#fff'>
                                    0.55 BNB
                                </Typography>
                            </Box>
                            <Box marginY='1rem' display='flex' alignItems='center'>
                                <Inputbase type='number' placeholder='0.30 BNB' required />
                                <Box>
                                    <Mintnew onClick={play}>
                                        Mint Now
                                    </Mintnew>
                                </Box>
                            </Box>
                        </Box>
                    </Box>
                </Box>
            </Modal>
        </div>
    )
}

export default MintLeft;