import { Box, Container, Grid, Typography } from '@mui/material'
import React from 'react'

const FloorPrice = () => {
    return (
        <div>
            <Box paddingY='3rem'>
                <Container maxWidth='lg'>
                    <Grid container spacing={2}>
                        <Grid item md='3'>
                            <Box textAlign='center'>
                                <Typography variant='h4' fontWeight='bold' gutterBottom component='h4'>
                                    1500
                                </Typography>
                                <Typography variant='h6' fontWeight='bold' gutterBottom component='h6'>
                                    Total Items
                                </Typography>
                            </Box>
                        </Grid>
                        <Grid item md='3'>
                            <Box textAlign='center'>
                                <Typography variant='h4' fontWeight='bold' gutterBottom component='h4'>
                                    1000+
                                </Typography>
                                <Typography variant='h6' fontWeight='bold' gutterBottom component='h6'>
                                    Total Owners
                                </Typography>
                            </Box>
                        </Grid>
                        <Grid item md='3'>
                            <Box textAlign='center'>
                                <Typography variant='h4' fontWeight='bold' gutterBottom component='h4'>
                                    0.05
                                </Typography>
                                <Typography variant='h6' fontWeight='bold' gutterBottom component='h6'>
                                Floor Price (BNB)
                                </Typography>
                            </Box>
                        </Grid>
                        <Grid item md='3'>
                            <Box textAlign='center'>
                                <Typography variant='h4' fontWeight='bold' gutterBottom component='h4'>
                                    25.85k
                                </Typography>
                                <Typography variant='h6' fontWeight='bold' gutterBottom component='h6'>
                                Floor Price (BNB)
                                </Typography>
                            </Box>
                        </Grid>
                    </Grid>
                </Container>
            </Box>
        </div>
    )
}

export default FloorPrice;