import { Box, Container, Grid, Typography, } from '@mui/material';
import React from 'react'
import styled from 'styled-components';
import RewardsPaid from './RewardsPaid';
import useSound from 'use-sound';
import sounds from '../../../assets/t-rex-roar.mp3'

import mintlive from '../../../assets/mint_live_text.png'
import beasr from '../../../assets/Hot-Collection/bear.png'

const Mainpage = styled.div``
const Access = styled.div`
    color: ${({ theme }) => theme.textmulti};
    font-size: 24px;
    font-weight: 500;
`
const Mintnft = styled.button`
    padding: 10px 20px;
    border: 1px solid ${({ theme }) => theme.bgBtns};
    background: ${({ theme }) => theme.bgBtns};
    color:  ${({ theme }) => theme.bg};
    border-radius: 1px;
    font-size: 20px;
    cursor: pointer;
    transition: 0.5s; 
    letter-spacing:2px;
    &:hover {
        background: ${({ theme }) => theme.bg};
        color: ${({ theme }) => theme.bgBtns};
        border: 1px solid ${({ theme }) => theme.bgBtns}
    }
`


const Home = () => {

    const [play] = useSound(sounds);

    return (
        <div>
            <Mainpage className='herosbg'>
                <Box sx={{ paddingY: { md: '8rem', xs: '3rem' } }}>
                    <Container maxWidth='lg'>
                        <Grid container spacing={2}>
                            <Grid item md={6} xs={12}>
                                <Box position='relative' >
                                    <Typography variant='h2' fontWeight='bold' gutterBottom component='h2'>
                                        MM3d
                                        NFT COLLECTIONS
                                    </Typography>
                                    <Access>
                                        Access Mint NFT earning rewards
                                    </Access>

                                    <Box paddingY='2rem'>
                                        <RewardsPaid />
                                    </Box>
                                    <Box>
                                        <Mintnft onClick={play}>
                                            Mint NFT
                                        </Mintnft>
                                    </Box>
                                    <Box paddingTop='1rem'>
                                        <Access>
                                            MAX 2 NFTS PER WALLET . PRICE 0.09 BNB + GAS
                                            MINT IS LIVE UNTIL 25 Set. 04:00H
                                        </Access>
                                    </Box>
                                </Box>
                            </Grid>
                            <Grid item md={6} xs={12}>
                                <Box position='relative'>
                                    <Box>
                                        <span className='rotated-style'>
                                            <img className='mint_live_text' src={mintlive} alt='mint live image' />
                                        </span>
                                    </Box>
                                    <Box>
                                        <Box></Box>
                                        <Box>
                                            <img src={beasr} width='100%' alt='mint live image' style={{borderRadius:'12px'}} />
                                        </Box>
                                    </Box>
                                </Box>
                            </Grid>
                        </Grid>
                    </Container>
                </Box>
            </Mainpage>
        </div>
    )
}

export default Home;